@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
.header {
  background-image: url(/static/media/image-hero.3de81110.jpg);
  height: 50vh;
  width: 100%;
}
.textHeader-font {
  font-family: "Josefin Sans", sans-serif;
}

.header-below {
  display: flex;
  flex-direction: row;
  position: relative;
  justify-content: space-between;
}

.text-header-below {
  position: absolute;
  width: 500px;
  height: 280px;
  top: 100px;
  right: 200px;
  background-color: white;
  padding: 50px;
  font-family: "Alata", sans-serif;
}
.text-header-below h1 {
  font-family: "Josefin Sans", sans-serif;
}

.our-creations {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.our-creations-button {
  font-family: "Alata", sans-serif;
}
.our-creations-text {
  font-family: "Josefin Sans", sans-serif;
}

.creation-images-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 5px;
}
footer {
  background-color: black;
}
.footer-menu {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.footer-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  justify-items: center;
}
.curiosity {
  background-image: url(/static/media/image-curiosity.40d51f38.jpg);
  width: 100%;
  height: 50vh;
}
.deepEarth {
  background-image: url(/static/media/image-deep-earth.f8da6014.jpg);
  width: 100%;
  height: 50vh;
}
.fishEye {
  background-image: url(/static/media/image-fisheye.8bb3f74e.jpg);
  width: 100%;
  height: 50vh;
}
.fromAbove {
  background-image: url(/static/media/image-from-above.f316f3f3.jpg);
  width: 100%;
  height: 50vh;
}
.grid {
  background-image: url(/static/media/image-grid.043c26ef.jpg);
  width: 100%;
  height: 50vh;
}
.nightArcade {
  background-image: url(/static/media/image-night-arcade.a5acf9d0.jpg);
  width: 100%;
  height: 50vh;
}
.pocketBorealis {
  background-image: url(/static/media/image-pocket-borealis.0b89db1f.jpg);
  width: 100%;
  height: 50vh;
}

.soccerTeam {
  background-image: url(/static/media/image-soccer-team.24ac0872.jpg);
  width: 100%;
  height: 50vh;
}

@media (max-width: 991px) {
  .creation-images-grid {
    display: grid;
    grid-template-columns: 1fr;
  }

  .footer-grid {
    display: grid;
    grid-template-columns: 1fr;
  }
  .footer-menu {
    display: flex;
    flex-direction: column;
  }
  .footer-icons {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-content: center;
  }
  .curiosity {
    background-image: url(/static/media/image-curiosity.7f14665a.jpg);
    width: 100%;
    height: 10vh;
  }
  .deepEarth {
    background-image: url(/static/media/image-deep-earth.2a157951.jpg);
    width: 100%;
    height: 10vh;
  }
  .fishEye {
    background-image: url(/static/media/image-fisheye.ff97b54e.jpg);
    width: 100%;
    height: 10vh;
  }
  .fromAbove {
    background-image: url(/static/media/image-from-above.b7b9dba0.jpg);
    width: 100%;
    height: 10vh;
  }
  .grid {
    background-image: url(/static/media/image-grid.a8ce4f2b.jpg);
    width: 100%;
    height: 10vh;
  }
  .nightArcade {
    background-image: url(/static/media/image-night-arcade.a4f25d3b.jpg);
    width: 100%;
    height: 10vh;
  }
  .pocketBorealis {
    background-image: url(/static/media/image-pocket-borealis.799292df.jpg);
    width: 100%;
    height: 10vh;
  }

  .soccerTeam {
    background-image: url(/static/media/image-soccer-team.18a63d8d.jpg);
    width: 100%;
    height: 10vh;
  }

  .header-below {
    display: flex;
    flex-direction: column;
    position: static;
    padding-bottom: 2rem;
  }
  .text-header-below {
    position: static;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .image-header-below {
    width: 100%;
    position: static;
  }
  .our-creations {
    justify-content: center;
  }

  .our-creations input {
    display: none;
  }

  .textHeader-font {
    position: absolute;
    display: grid;

    width: 50%;
    top: 50px;
    right: 80px;
  }

  .textHeader-font h1 {
    font-size: 2rem;
  }
}

